function takeRadioGroupViewProperties(radioGroup: RadioGroup): object {
  const options: RadioGroupOption[] = radioGroup.options ?? [];
  return {
    [HideLabelKey]: !radioGroup.showLabel,
    [AddOtherKey]: radioGroup.customOption != null,
    [NumberOfColumnsKey]: numericNumberOfColumns(
      radioGroup.numberOfColumns ?? NumberOfColumns.ONE,
    ),
    [OptionsKey]: options.map((option: any) => makeOptionView(option)),
    ...(radioGroup.description && { [DescriptionKey]: radioGroup.description }),
    ...(radioGroup.label && { [LabelKey]: radioGroup.label }),
    ...(radioGroup.customOption && {
      [AddOtherLabelKey]: radioGroup.customOption.label,
      [AddOtherPlaceholderKey]: radioGroup.customOption.placeholder,
    }),
    ...(options.find((option: any) => option.default) && {
      [DefaultValueKey]: options.find((option) => option.default)?.value,
    }),
  };
}

