function resolvePaymentComponent(paymentComponent: Payment): object {
  switch (paymentComponent.componentType) {
    case PaymentComponentType.CHECKBOX_GROUP:
      return takeProductCheckboxGroupViewProperties(
        paymentComponent.checkboxGroupOptions!,
      );
    case PaymentComponentType.DONATION_INPUT:
      return takeDonationsInputViewProperties(
        paymentComponent.donationInputOptions!,
      );
    case PaymentComponentType.FIXED_PAYMENT:
      return takeFixedPaymentViewProperties(
        paymentComponent.fixedPaymentOptions!,
      );
    case PaymentComponentType.PAYMENT_INPUT:
      return takePaymentInputViewProperties(
        paymentComponent.paymentInputOptions!,
      );
    default:
      throw new Error(
        `Unknown payment component type: ${paymentComponent.componentType}`,
      );
  }
}

function resolveMultilineAddressComponent(multilineAddress: Address): object {
  switch (multilineAddress.componentType) {
    case AddressComponentType.MULTILINE_ADDRESS:
      return takeMultilineAddressViewProperties(
        multilineAddress.multilineAddressOptions!,
      );
    default:
      throw new Error(
        `Unknown multiline address component type: ${multilineAddress.componentType}`,
      );
  }
}

function resolveSchedulingComponent(schedulingOptions: Scheduling): object {
  switch (schedulingOptions.componentType) {
    case SchedulingComponentType.APPOINTMENT:
      return takeAppointmentViewProperties(
        schedulingOptions.appointmentOptions!,
      );
    default:
      throw new Error(
        `Unknown scheduling component type: ${schedulingOptions.componentType}`,
      );
  }
}

function takeMultilineAddressViewProperties(
  multilineAddress: MultilineAddress,
): object {
  const { showLabel, ...multilineAddressView } = multilineAddress;
  return { ...multilineAddressView, hideLabel: !showLabel };
}

function takeAppointmentViewProperties(
  appointmentOptions: Appointment,
): object {
  const {
    label,
    name,
    durationInMinutes,
    manualApprovalRequired,
    staffIds,
    format,
    description,
    showLabel,
    phoneOptions,
    videoConferenceOptions,
    inPersonOptions,
  } = appointmentOptions;
  return {
    ...(label && { [LabelKey]: label }),
    ...(name && { [NameKey]: name }),
    ...(durationInMinutes && { [DurationKey]: durationInMinutes }),
    ...(manualApprovalRequired !== undefined && {
      [ManualApprovalRequiredKey]: manualApprovalRequired,
    }),
    ...(description && { [DescriptionKey]: description }),
    ...(showLabel !== undefined && { [HideLabelKey]: !showLabel }),
    [FormatKey]: format,
    [FormatDescriptionTypeKey]: format,
    [StaffIdsKey]: staffIds,
    ...transformFormatInfoToView(
      inPersonOptions,
      videoConferenceOptions,
      phoneOptions,
    ),
  };
}

function transformFormatInfoToView(
  inPersonOptions?: InPersonOptions,
  videoConferenceOptions?: VideoConferenceOptions,
  phoneOptions?: PhoneOptions,
): object {
  if (phoneOptions?.description) {
    return { [FormatDescriptionKey]: phoneOptions.description };
  }
  if (videoConferenceOptions?.description) {
    return { [FormatDescriptionKey]: videoConferenceOptions.description };
  }
  if (inPersonOptions) {
    const { locations } = inPersonOptions;
    const customLocation = (locations ?? []).find((loc) => loc.customAddress);
    if (customLocation) {
      return {
        [FormatDescriptionKey]: customLocation.customAddress,
        [LocationTypeKey]: 'CUSTOM_LOCATION',
      };
    }
    const businessIds = (locations ?? [])
      .map((loc) => loc.businessLocationId)
      .filter(Boolean);
    if (businessIds.length > 0) {
      return {
        [LocationIdsKey]: businessIds,
        [LocationTypeKey]: 'OS_LOCATION',
      };
    }
  }
  return {};
}

function takeDonationsInputViewProperties(
  donationInput: DonationInput,
): object {
  const options = donationInput.options ?? [];
  return {
    [HideLabelKey]: !donationInput.showLabel,
    [AddOtherKey]: donationInput.customOption != null,
    [NumberOfColumnsKey]: numericNumberOfColumns(
      donationInput.numberOfColumns ?? NumberOfColumns.ONE,
    ),
    [OptionsKey]: options.map((option: any) => makeOptionView(option)),
    ...(donationInput.description && {
      [DescriptionKey]: donationInput.description,
    }),
    ...(donationInput.label && { [LabelKey]: donationInput.label }),
    ...(donationInput.customOption && {
      [AddOtherLabelKey]: donationInput.customOption.label,
      [AddOtherPlaceholderKey]: donationInput.customOption.placeholder,
    }),
    ...(options.find((option: any) => option.default) && {
      [DefaultValueKey]: options.find((option) => option.default)?.value,
    }),
  };
}

function takeFixedPaymentViewProperties(fixedPayment: FixedPayment): object {
  return {
    [HideLabelKey]: !fixedPayment.showLabel,
    ...(fixedPayment.label && { [LabelKey]: fixedPayment.label }),
    ...(fixedPayment.description && {
      [DescriptionKey]: fixedPayment.description,
    }),
  };
}

function takePaymentInputViewProperties(paymentInput: PaymentInput): object {
  return {
    [HideLabelKey]: !paymentInput.showLabel,
    ...(paymentInput.label && { [LabelKey]: paymentInput.label }),
    ...(paymentInput.description && {
      [DescriptionKey]: paymentInput.description,
    }),
    ...(paymentInput.placeholder && {
      [PlaceholderKey]: paymentInput.placeholder,
    }),
  };
}

function makeOptionView(option: any): Record<string, any> {
  return {
    [LabelKey]: option.label,
    [ValueKey]: option.value,
    [idKey]: option.id,
    ...(option.media && { [MediaKey]: option.media }),
  };
}

function numericNumberOfColumns(numberOfColumnsEnum: string): number {
  switch (numberOfColumnsEnum) {
    case 'TWO':
      return 2;
    case 'THREE':
      return 3;
    default:
      return 1;
  }
}
