import React, { type ReactElement, useMemo, useState } from 'react';
import { useForm as useFormViewer } from '@wix/form-viewer';
import { type Layout } from '@wix/form-fields';

import { type UseFormProps } from '../types/types';
import { normalizeSchema } from '../utils/normalize-schema';
import { type InputElement } from '@wix/form-fields/dist/types/ui/form/types';
import { FIELD_PROP_MAP } from '../mappers/props-map';
import { FIELD_TYPE_MAP } from '../mappers/field-type-map';
import { mapFieldProps, mapFieldTypes } from '../utils/mappers';

type UseFormReturn = {
  columnCount: number;
  fieldElements: ReactElement[][];
  fieldsLayout: Record<string, Layout>;
} | null;

export const useForm = ({
  form: unformattedForm,
  values,
  onChange,
  errors,
  onValidate,
  submitForm,
  fieldMap: fields,
}: UseFormProps): UseFormReturn => {
  const form = normalizeSchema(unformattedForm);
  const [stepId, setStepId] = useState<string>();

  const actions = {
    submitForm,
    uploadFile: () => {
      // eslint-disable-next-line no-console
      console.log('upload file not implemented');
    },
  };

  const mappedFieldProps = useMemo(
    () => mapFieldProps(fields, FIELD_PROP_MAP),
    [fields],
  );

  const mappedFieldTypes = useMemo(
    () => mapFieldTypes(mappedFieldProps, FIELD_TYPE_MAP),
    [mappedFieldProps],
  );

  const { currentView, formFields } = useFormViewer({
    form,
    actions,
    values,
    stepId,
    onStepIdChange: setStepId,
    onChange,
    errors,
    onValidate,
    fieldMap: mappedFieldTypes,
  });

  const fieldsLayout = currentView.fields.reduce<Record<string, Layout>>(
    (acc, field) => {
      acc[field.id] = field.layout;
      return acc;
    },
    {},
  );

  const fieldElementsByRow = useMemo(() => {
    const fieldsByRow = new Map<number, ReactElement[]>();

    formFields.forEach(({ component }: InputElement, index: number) => {
      const field = currentView.fields[index];
      const row = field?.layout.row ?? 0;

      if (!fieldsByRow.has(row)) {
        fieldsByRow.set(row, []);
      }

      fieldsByRow.get(row)!.push(component);
    });

    return Array.from(fieldsByRow.entries())
      .sort(([rowA], [rowB]) => rowA - rowB)
      .map(([_, elements]) => elements);
  }, [formFields, currentView.fields]);

  return form
    ? {
        columnCount: currentView.grid.columns,
        fieldElements: fieldElementsByRow,
        fieldsLayout,
      }
    : null;
};
