export function asRequired<T, U>(
  value: T | null | undefined,
  parent: U,
  property: string,
): T {
  if (typeof asOptional(value) !== 'undefined') {
    return value as T;
  }
  throw new Error(
    `Property is required in ${JSON.stringify(parent)}, property: ${property}`,
  );
}

export function asOptional<T>(value: T | null | undefined): T | undefined {
  return value == null ? undefined : value;
}

export function isDefined<T>(value?: T | null): value is T {
  return asOptional(value) !== undefined;
}

export function isNullOrUndefined<T>(value: T | null | undefined): Boolean {
  return value === null || value === undefined;
}

export function setValidationProperties<T = unknown>(
  properties: string[],
  field: object,
  propertyManipulations: { [field: string]: (value: T) => T } = {},
) {
  return properties.reduce<{ [key: string]: unknown }>((acc, key) => {
    const userValue = (field as { [key: string]: T })[key];
    const value = propertyManipulations[key]
      ? propertyManipulations[key](userValue)
      : userValue;
    if (
      typeof asOptional(value) !== 'undefined' &&
      !isEnumValidationPropertyArrayEmpty(key, value)
    ) {
      acc[key] = value;
    }
    return acc;
  }, {});
}

export function setValidationMessages(
  properties: string[],
  field: { validationMessages?: object },
): { errorMessage?: { [key: string]: string } } {
  if (field.validationMessages) {
    const customMessages = field.validationMessages as {
      [key: string]: string;
    };
    const errorMessage = properties.reduce<{ [key: string]: string }>(
      (acc, property) => {
        const propertyErrorMsg = customMessages[property];
        if (propertyErrorMsg) {
          acc[property] = propertyErrorMsg;
        }
        return acc;
      },
      {},
    );
    return {
      errorMessage,
    };
  }
  return {};
}

export function notEmpty<TValue>(value: TValue | null): value is TValue {
  return value !== null;
}

export function isObject(givenValue: any): givenValue is object {
  return (
    typeof givenValue === 'object' &&
    !Array.isArray(givenValue) &&
    givenValue !== null
  );
}

function isEnumValidationPropertyArrayEmpty(
  key: string,
  value: unknown,
): boolean {
  return key === 'enum' && Array.isArray(value) && !value.length;
}
