import {
  ErrorTypes,
  makeValidationErrors,
  type ValidationError,
  type ValidationResult,
} from './errors';
import {
  type Form,
  ValidationFormat,
} from '@wix/ambassador-forms-v4-form/types';

import { createAjvSchemaValidatorFactory } from './create-ajv-schema-validator';
import { formSchemaMapperFactory } from './mappers/form-schema-mapper';
import { propertySchemaMapper } from './mappers/property-schema-mapper';
import { type Submission } from '../../submission';
import {
  type FormField,
  ValidationMode,
} from '@wix/ambassador-forms-v1-validation-result/types';
import { isObject } from '../utils';
import type Ajv from 'ajv';
import { transformPlatformisedFieldToValidation } from './mappers/platformised-fields/transform-platformised-field-to-validation';
import { formSchemaMapperFactoryPlatformised } from './mappers/platformised-fields/form-schema-mapper-platformised';

export class FormValidator {
  constructor(
    private readonly formSchemas: Form[],
    private readonly validationMode?: ValidationMode,
    private readonly platformisedFieldBasedValidation: Boolean = false,
    private readonly addressFormProvider: AddressFormProvider = () => undefined,
    private readonly submissionValueProvider?: (
      submission: Submission,
      form: Form,
    ) => Submission,
    private readonly libraryBasedPhoneFieldValidationEnabled: boolean = false,
    private readonly lenientPhoneValidation: boolean = false,
  ) {
    if (!this.formSchemas.length) {
      throw new Error('At least one form schema is required for validation');
    }
  }

  validateFields(
    formId: string,
    _submission: Submission,
    fieldIdMask?: string | string[],
  ) {
    const fieldMasks = [fieldIdMask ?? []].flat();
    const { ajv, submission, currentlyValidatableForm } =
      this.prepareValidation(formId, _submission);
    const normalizedSubmission = this.ignoreNullFields(submission);

    if (fieldMasks.length) {
      const errors = this.validateSubmissionWithFieldMask(
        ajv,
        formId,
        normalizedSubmission,
        fieldMasks,
      );
      return { errors, currentlyValidatableForm };
    }

    return {
      errors: this.validateWholeSubmission(
        ajv,
        currentlyValidatableForm.id!,
        normalizedSubmission,
      ),
      currentlyValidatableForm,
    };
  }

  validate(
    formId: string,
    _submission: Submission,
    fieldIdMask?: string | string[],
  ): ValidationResult {
    const fieldMasks = [fieldIdMask ?? []].flat();

    const { errors, currentlyValidatableForm } = this.validateFields(
      formId,
      _submission,
      fieldMasks,
    );

    if (errors.length > 0) {
      return { errors };
    }

    if (!fieldMasks.length) {
      const canUpdateForm = this.validateCanUpdateFormNotDisabled(
        currentlyValidatableForm,
      );

      if (canUpdateForm.errors) {
        return canUpdateForm;
      }
    }

    return { errors: [] };
  }

  private prepareValidation(formId: string, _submission: Submission) {
    const currentlyValidatableForm = this.formSchemas.find(
      (form) => form.id === formId,
    );
    if (!currentlyValidatableForm) {
      throw new Error(`Missing form ${formId} for validation`);
    }

    const submission = this.submissionValueProvider
      ? this.submissionValueProvider(_submission, currentlyValidatableForm)
      : _submission;

    const ajv = this.initAJvWithSchemas(currentlyValidatableForm, submission);

    return { ajv, submission, currentlyValidatableForm };
  }

  private getAddressForms(formSchemas: Form, submission: Submission): Form[] {
    const addressFields = [
      ...(formSchemas.fields ?? []),
      ...(formSchemas.deletedFields ?? []),
    ].filter(
      (field) =>
        field.validation?.predefined?.format ===
        ValidationFormat.MULTILINE_ADDRESS,
    );
    return addressFields.reduce((acc, field) => {
      const value = submission[field.target!];
      const addressForm = this.addressFormProvider(field, value);
      if (addressForm) {
        acc.push({ ...addressForm, id: field.target! } as Form);
      }
      return acc;
    }, [] as Form[]);
  }

  private getObjectArrayForms(formSchemas: Form): Form[] {
    const objectArrayFields = [
      ...(formSchemas.fields ?? []),
      ...(formSchemas.deletedFields ?? []),
    ].filter(
      (field) =>
        field.validation?.predefined?.format === ValidationFormat.OBJECT_ARRAY,
    );

    return objectArrayFields.map((field) => {
      return { id: field.target, fields: field.view?.fields ?? [] };
    });
  }

  private validateCanUpdateFormNotDisabled(form: Form) {
    if (
      this.validationMode !== ValidationMode.UNRESTRICTED_UPDATE &&
      this.isFormDisabled(form)
    ) {
      return {
        errors: [
          {
            errorPath: 'form.properties.disabled',
            errorType: ErrorTypes.DISABLED_FORM_ERROR,
            errorMessage: 'Form is disabled and not accepting submissions',
          },
        ],
      };
    } else {
      return {};
    }
  }

  private validateWholeSubmission(
    ajv: Ajv,
    formId: string,
    fieldSubmission: Submission,
  ): ValidationError[] {
    const validate = ajv.getSchema(`${formId}`);
    if (!validate) {
      throw new Error(`Missing precompiled schema for form ${formId}`);
    }
    validate(fieldSubmission);
    return makeValidationErrors(validate.errors);
  }

  private validateSubmissionWithFieldMask(
    ajv: Ajv,
    formId: string,
    fieldSubmission: Submission,
    fieldIdMasks: string[],
  ) {
    return fieldIdMasks.reduce<ValidationError[]>((acc, mask) => {
      const validate = ajv.getSchema(`${formId}#/properties/${mask}`);
      if (!validate) {
        acc.push({
          errorPath: `${mask}`,
          errorType: ErrorTypes.UNKNOWN_VALUE_ERROR,
          errorMessage: `${mask} property not expected in schema`,
        });
        return acc;
      }

      const valid = validate(fieldSubmission[mask]);

      if (!valid) {
        acc.push(...makeValidationErrors(validate.errors, mask));
      }
      return acc;
    }, []);
  }

  private isFormDisabled(formSchema: Form): boolean {
    const isDeadlinePast =
      formSchema?.limitationRule?.dateTimeDeadline &&
      new Date(formSchema.limitationRule.dateTimeDeadline).getTime() <=
        new Date().getTime();
    return Boolean(formSchema?.properties?.disabled || isDeadlinePast);
  }

  private ignoreNullFields(fieldSubmission: Submission): Submission {
    if (isObject(fieldSubmission)) {
      return Object.keys(fieldSubmission)
        .filter(
          (k) =>
            fieldSubmission[k] != null &&
            !this.isEmptyArray(fieldSubmission[k]),
        )
        .reduce(
          (a, k) => ({
            ...a,
            [k]: this.ignoreNullFields(fieldSubmission[k] as Submission),
          }),
          {},
        );
    } else {
      return fieldSubmission;
    }
  }

  private isEmptyArray(value: unknown) {
    return Array.isArray(value) && !value.length;
  }

  private getFormSchemaMapper() {
    return this.platformisedFieldBasedValidation
      ? formSchemaMapperFactoryPlatformised(
          transformPlatformisedFieldToValidation,
          this.validationMode ?? ValidationMode.UNKNOWN,
          this.libraryBasedPhoneFieldValidationEnabled,
        )
      : formSchemaMapperFactory(
          propertySchemaMapper,
          this.validationMode ?? ValidationMode.UNKNOWN,
          this.libraryBasedPhoneFieldValidationEnabled,
        );
  }

  private initAJvWithSchemas(
    currentlyValidatableForm: Form,
    submission: Submission,
  ) {
    const formSchemaMapper = this.getFormSchemaMapper();
    const createAjvSchemaValidator = createAjvSchemaValidatorFactory(
      formSchemaMapper,
      this.libraryBasedPhoneFieldValidationEnabled &&
        this.validationMode !== ValidationMode.UNRESTRICTED_UPDATE,
      this.lenientPhoneValidation,
    );

    const nestedForms = this.formSchemas.flatMap((schema) =>
      (schema.nestedForms || []).flatMap(({ targets, form }) =>
        (targets || []).map((target) => ({ ...form, id: target })),
      ),
    );

    const addressForms = this.getAddressForms(
      currentlyValidatableForm,
      submission,
    );

    const objectArrayForms = this.getObjectArrayForms(currentlyValidatableForm);

    return createAjvSchemaValidator([
      ...this.formSchemas,
      ...nestedForms,
      ...addressForms,
      ...objectArrayForms,
    ]);
  }
}

type AddressFormProvider = (
  field: FormField,
  submissionValue: unknown,
) => Form | undefined;
