import {
  type InputField,
  InputType,
  type PropertiesType as PropertiesTypeV2,
  type StringType as StringTypeV2,
  type ArrayItems as ArrayItemsV2,
  type ObjectType as ObjectTypeV2,
  type ArrayType as ArrayTypeV2,
  Format as FormatV2,
} from '@wix/ambassador-forms-v2-form-template/types';

import { type FieldsInputField as PlatformizedInputField } from '@wix/ambassador-forms-v4-form/types';

import {
  type Validation,
  type PropertiesType,
  type ArrayItems,
  type ObjectType,
  type ArrayType,
  ValidationFormat,
  Format,
  type StringType,
} from '@wix/ambassador-forms-v1-form-template/types';

export function makeValidationOfInputField(
  inputOptions: InputField | PlatformizedInputField,
): Validation {
  return {
    required: inputOptions.required,
    ...makeValidationByType(inputOptions),
  };
}

export function makeValidationByType(
  inputOptions: InputField | PlatformizedInputField,
): Validation {
  switch (inputOptions.inputType) {
    case InputType.STRING:
      return {
        string: transformStringType(inputOptions.stringOptions!.validation),
      };
    case InputType.NUMBER:
      return {
        number: inputOptions.numberOptions!.validation,
      };
    case InputType.BOOLEAN:
      return {
        boolean: inputOptions.booleanOptions!.validation,
      };
    case InputType.ARRAY:
      const arrayType: ArrayTypeV2 = inputOptions.arrayOptions!.validation!;
      return {
        array: transformArrayType(arrayType),
      };
    case InputType.OBJECT:
      const objectType: ObjectTypeV2 = inputOptions.objectOptions!.validation!;
      return {
        object: transformObjectType(objectType),
      };
    case InputType.PAYMENT:
      return {
        predefined: {
          format: ValidationFormat.PAYMENT,
          paymentOptions: inputOptions.paymentOptions!.validation,
        },
      };
    case InputType.WIX_FILE:
      return {
        predefined: {
          format: ValidationFormat.WIX_FILE,
        },
      };
    case InputType.ADDRESS:
      return {
        predefined: {
          format: ValidationFormat.MULTILINE_ADDRESS,
          multilineAddressOptions: inputOptions.addressOptions!.validation,
        },
      };
    case InputType.MULTILINE_ADDRESS:
      return {
        predefined: {
          format: ValidationFormat.MULTILINE_ADDRESS,
          multilineAddressOptions:
            inputOptions.multilineAddressOptions!.validation,
        },
      };
    case InputType.SCHEDULING:
      return {
        predefined: {
          format: ValidationFormat.SCHEDULING,
        },
      };
    default:
      throw new Error(`Unknown input type ${inputOptions.inputType}`);
  }
}

function transformArrayType(arrayType: ArrayTypeV2): ArrayType {
  return {
    maxItems: arrayType.maxItems,
    minItems: arrayType.minItems,
    items: transformArrayItems(arrayType.items!),
  };
}

function transformPropertiesType(
  propertiesV2: PropertiesTypeV2,
): PropertiesType {
  return {
    string: transformStringType(propertiesV2.stringOptions),
    number: propertiesV2.numberOptions,
    boolean: propertiesV2.booleanOptions,
    ...(propertiesV2.arrayOptions && {
      array: transformArrayType(propertiesV2.arrayOptions),
    }),
    required: propertiesV2.required,
  };
}

function transformArrayItems(items: ArrayItemsV2): ArrayItems {
  return {
    string: transformStringType(items.stringOptions),
    number: items.numberOptions,
    boolean: items.booleanOptions,
    object: transformObjectType(items.objectOptions),
  };
}

function transformObjectType(
  objectType?: ObjectTypeV2,
): ObjectType | undefined {
  if (objectType) {
    return {
      properties: Object.entries(objectType.properties!).reduce(
        (acc, [key, value]) => {
          acc[key] = transformPropertiesType(value);
          return acc;
        },
        {} as { [key: string]: PropertiesType },
      ),
    };
  }
  return undefined;
}

function transformStringType(stringTypeV2?: StringTypeV2): StringType {
  return {
    ...stringTypeV2,
    format: transformStringFormat(stringTypeV2?.format),
  };
}

function transformStringFormat(formatV2?: FormatV2): Format {
  switch (formatV2) {
    case FormatV2.UNKNOWN_FORMAT:
      return Format.UNDEFINED;
    default:
      return formatV2 as unknown as Format;
  }
}
