import React from 'react';
import { type FormFields as ViewerFields } from '@wix/form-fields';

import {
  type FieldType,
  type SchemaFieldType,
  type FieldMap,
} from '../types/form-fields';
import { type FieldMappers } from '../types/types';

export const mapFieldProps = (
  fields: FieldMap,
  mappers: FieldMappers,
): ViewerFields =>
  Object.fromEntries(
    Object.entries(fields).map(([fieldType, Component]) => [
      fieldType,
      mappers[fieldType as FieldType]
        ? (props: any) => (
            <Component
              {...(mappers[fieldType as FieldType](props) as unknown as any)}
            />
          )
        : Component,
    ]),
  ) as ViewerFields;

export const mapFieldTypes = (
  fields: ViewerFields,
  typeMap: Record<SchemaFieldType, string>,
): Record<string, React.ComponentType<any>> => ({
  ...fields,
  ...Object.fromEntries(
    Object.entries(typeMap)
      .filter(([, targetType]) => fields[targetType])
      .map(([newName, targetType]) => [newName, fields[targetType]]),
  ),
});
