import React, { type FC } from 'react';
import { useFieldView } from '../contexts/form-view-context';
import { isInputField } from '../ui/form/types';
import styles from './default-field-layout.module.scss';
import { type FieldLayoutProps } from './field-layout';

export const DefaultFieldLayout: FC<FieldLayoutProps> = ({
  fieldId,
  ...rest
}) => {
  const fieldView = useFieldView(fieldId);
  if (!fieldView) {
    return null;
  }

  const { layout, fieldType } = fieldView;
  const dataHook = `form-field-${
    isInputField(fieldView) ? fieldView.target : fieldView.id
  }`;

  const dataAttributes = {
    'data-hook': dataHook,
    'data-field-type': fieldType,
  };

  const rows = [1, 2];
  const gridRow = `1 / span ${rows.length}`;
  const gridColumn = `${layout.column + 1} / span ${layout.width}`;
  const [labelRow, inputRow] = rows.map(
    (currentRow) => `${currentRow} / span 1`,
  );

  if ('children' in rest) {
    return (
      <div style={{ gridRow, gridColumn }} {...dataAttributes}>
        {rest.children}
      </div>
    );
  }

  const { renderLabel, renderInput, renderDescription } = rest;

  return (
    <>
      <div style={{ gridRow: labelRow, gridColumn }} className={styles.label}>
        {renderLabel()}
      </div>
      <div style={{ gridRow: inputRow, gridColumn }} {...dataAttributes}>
        {renderInput()}
        {renderDescription()}
      </div>
    </>
  );
};
