import { type SubmitButtonProps } from '../types/field-props';
import { type FieldProps } from '../types/form-fields';
import { type FieldMappers, type PropsMapper } from '../types/types';

const MinMaxLengthMapper = (validation: any) => ({
  maxLength: validation?.string?.maxLength,
  minLength: validation?.string?.minLength,
});

const MinMaxItemsMapper = (validation: any) => ({
  minItems: validation?.array?.minItems,
  maxItems: validation?.array?.maxItems,
});

const MinMaxValuesMapper = (validation: any) => ({
  minValue:
    validation.predefined.paymentOptions.products[0].dynamicPriceOptions
      .minPrice,
  maxValue:
    validation.predefined.paymentOptions.products[0].dynamicPriceOptions
      .maxPrice,
});

const AcceptedDatesMapper = (validation: any) => ({
  acceptedDates:
    validation?.string?.dateOptions?.minimum === '$now'
      ? 'future'
      : validation?.string?.dateOptions?.maximum === '$now'
      ? 'past'
      : 'all',
});

const CustomOptionMapper = (props: any) => {
  const { addOther, addOtherLabel, addOtherPlaceholder } = props;
  return {
    customOption: addOther
      ? {
          label: addOtherLabel,
          placeholder: addOtherPlaceholder,
        }
      : undefined,
  };
};

export const ErrorMapper: PropsMapper = (props) => {
  return {
    ...props,
    // TODO
    error: '',
  };
};

export const BaseMapper: PropsMapper = (props) => {
  const { hideLabel } = props;
  return {
    ...props,
    showLabel: hideLabel === undefined ? true : !hideLabel,
    ...ErrorMapper(props),
  };
};

export const TextFieldMapper: PropsMapper = (props) => ({
  ...props,
  ...MinMaxLengthMapper(props?.validation),
  ...BaseMapper(props),
});
export const TextAreaMapper: PropsMapper = (props) => ({
  ...props,
  ...MinMaxLengthMapper(props?.validation),
  ...BaseMapper(props),
});
export const PhoneInputMapper: PropsMapper = (props) => {
  const { defaultCountry, validation } = props;
  return {
    ...props,
    ...BaseMapper(props),
    defaultCountryCode: defaultCountry,
    allowedCountryCodes: validation?.string?.phoneOptions?.allowedCountryCodes,
  };
};
export const MultilineAddressMapper: PropsMapper = (props) => {
  const { view, validation } = props;
  return {
    ...props,
    ...BaseMapper(props),
    showAddressLine2: view?.fieldSettings?.addressLine2?.show,
    addressLine2Required:
      validation?.predefined?.multilineAddressOptions?.fields?.addressLine2
        ?.required,
  };
};
export const DateInputMapper: PropsMapper = (props) => {
  const { hidePlaceholder, validation } = props;
  return {
    ...props,
    ...BaseMapper(props),
    ...AcceptedDatesMapper(validation),
    showPlaceholder: hidePlaceholder === undefined ? true : !hidePlaceholder,
  };
};
export const DatePickerMapper: PropsMapper = (props) => {
  const { validation } = props;
  return {
    ...props,
    ...BaseMapper(props),
    ...AcceptedDatesMapper(validation),
  };
};
export const DateTimeInputMapper: PropsMapper = (props) => {
  const { hidePlaceholder, validation } = props;
  return {
    ...props,
    ...BaseMapper(props),
    ...AcceptedDatesMapper(validation),
    showPlaceholder: hidePlaceholder === undefined ? true : !hidePlaceholder,
  };
};
export const FileUploadMapper: PropsMapper = (props) => {
  const { valueSliderLimit, uploadFileFormats } = props;
  return {
    ...props,
    ...BaseMapper(props),
    maxFiles: valueSliderLimit,
    allowedFileFormats: uploadFileFormats,
  };
};
export const NumberInputMapper: PropsMapper = (props) => BaseMapper(props);
export const CheckboxMapper: PropsMapper = (props) => ErrorMapper(props);
export const SignatureMapper: PropsMapper = (props) => {
  const { uploadEnabled } = props;
  return {
    ...props,
    ...BaseMapper(props),
    imageUploadEnabled: uploadEnabled,
  };
};
export const RatingInputMapper: PropsMapper = (props) => BaseMapper(props);
export const RadioGroupMapper: PropsMapper = (props) => {
  return {
    ...props,
    ...BaseMapper(props),
    ...CustomOptionMapper(props),
  };
};
export const CheckboxGroupMapper: PropsMapper = (props) => {
  const { validation } = props;
  return {
    ...props,
    ...BaseMapper(props),
    ...CustomOptionMapper(props),
    ...MinMaxItemsMapper(validation),
  };
};
export const DropdownMapper: PropsMapper = (props) => BaseMapper(props);
export const TagsMapper: PropsMapper = (props) => {
  const { validation } = props;
  return {
    ...props,
    ...BaseMapper(props),
    ...CustomOptionMapper(props),
    ...MinMaxItemsMapper(validation),
  };
};
export const TimeInputMapper: PropsMapper = (props) => {
  const { hidePlaceholder } = props;
  return {
    ...props,
    ...BaseMapper(props),
    showPlaceholder: hidePlaceholder === undefined ? true : !hidePlaceholder,
  };
};
export const TextMapper: PropsMapper = (props) => props;

export const SubmitButtonMapper: PropsMapper = (props): SubmitButtonProps => {
  return {
    ...props,
  };
};

export const ProductListMapper: PropsMapper = (props) => {
  return {
    ...props,
    ...BaseMapper(props),
    ...MinMaxItemsMapper(props?.validation),
  };
};
export const FixedPaymentMapper: PropsMapper = (props) => {
  const { validation, hideLabel } = props;
  const paymentValidation =
    validation?.predefined?.paymentOptions?.products?.[0];
  const fixedPaymentValue = paymentValidation?.fixedPriceOptions?.price;

  return {
    ...props,
    showLabel: hideLabel === undefined ? true : !hideLabel,
    amount: fixedPaymentValue,
  };
};
export const PaymentInputMapper: PropsMapper = (props) => {
  const { validation } = props;
  return {
    ...props,
    ...BaseMapper(props),
    ...MinMaxValuesMapper(validation),
  };
};
export const DonationMapper: PropsMapper = (props) => {
  return {
    ...props,
    ...BaseMapper(props),
    ...CustomOptionMapper(props),
  };
};
export const AppointmentMapper: PropsMapper = (props) => BaseMapper(props);
export const ImageChoiceMapper: PropsMapper = (props) => {
  const { isMultipleAnswers, choiceImageResize } = props;
  return {
    ...props,
    ...BaseMapper(props),
    multiple: isMultipleAnswers,
    imageAspectRatio: choiceImageResize,
  };
};

export const FIELD_PROP_MAP: FieldMappers = {
  TEXT_INPUT: TextFieldMapper,
  TEXT_AREA: TextAreaMapper,
  PHONE_INPUT: PhoneInputMapper,
  MULTILINE_ADDRESS: MultilineAddressMapper,
  DATE_INPUT: DateInputMapper,
  DATE_PICKER: DatePickerMapper,
  DATE_TIME_INPUT: DateTimeInputMapper,
  FILE_UPLOAD: FileUploadMapper,
  NUMBER_INPUT: NumberInputMapper,
  CHECKBOX: CheckboxMapper,
  SIGNATURE: SignatureMapper,
  RATING_INPUT: RatingInputMapper,
  RADIO_GROUP: RadioGroupMapper,
  CHECKBOX_GROUP: CheckboxGroupMapper,
  DROPDOWN: DropdownMapper,
  TAGS: TagsMapper,
  TIME_INPUT: TimeInputMapper,
  TEXT: TextMapper,
  SUBMIT_BUTTON: SubmitButtonMapper,
  PRODUCT_LIST: ProductListMapper,
  FIXED_PAYMENT: FixedPaymentMapper,
  PAYMENT_INPUT: PaymentInputMapper,
  DONATION: DonationMapper,
  APPOINTMENT: AppointmentMapper,
  IMAGE_CHOICE: ImageChoiceMapper,
  // fields that have not been implemented
  '': () => ({} as FieldProps),
};
