import {
  type FormField,
  type MultilineAddressValidation,
} from '@wix/ambassador-forms-v4-form/types';
import { COUNTRY_CODES } from './country-codes';

export function getCountryField(
  // required will be replaced with FieldSettings country once we have it in the proto
  required?: boolean,
  multilineAddressOptions?: MultilineAddressValidation,
): FormField {
  return {
    id: '1e445376-d280-4cda-9ffa-18b61c2881c7',
    target: 'country',
    pii: true,
    hidden: false,
    view: {
      fieldType: 'MLA_COUNTRY',
      options: multilineAddressOptions?.allowedCountries?.length
        ? multilineAddressOptions?.allowedCountries
        : Object.keys(COUNTRY_CODES).map((country) => ({ value: country })),
    },
    validation: {
      required: Boolean(required),
      string: {
        enum: multilineAddressOptions?.allowedCountries?.length
          ? multilineAddressOptions?.allowedCountries
          : Object.keys(COUNTRY_CODES),
      },
    },
  };
}

export function getCountryFieldLayout() {
  return {
    fieldId: '1e445376-d280-4cda-9ffa-18b61c2881c7',
    row: 0,
    column: 0,
    width: 12,
    height: 1,
  };
}
